:root {
    --primary: #370f65;
    --accent: #c09ce9;
    --midtone: #7320d3
}

ul {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    gap: 6px;
}

li:not(li.button) {
    padding: 10px;
}

li:nth-child(2n) {
    background: var(--accent);
}

li, li button {
    width: 200px;
    height: 200px;
    background:#fff;
    color: var(--primary);
    border-radius: 5px;
    display: flex;
    align-items: center;
    /* animation: entrance .25s linear; */
}

li button {
    border: none;
    background: var(--midtone);
    color: #FFF;
    padding: 10px;
    margin: 0;
    font-size: 1.5rem;
    width: 170px;
    height: 170px;
}

li.button {
    margin: 10px;
    background: transparent;
}

@keyframes entrance {
    0% {
        transform: rotateY(0deg);
    }

    95% {
        transform: rotateY(25deg);
    }

    100% {
        transform: rotateY(0deg);
    }
}

.form {
    width: 40%;
    height: 100vh;
    background: var(--accent);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.form button, .form input {
    border: none;
    border: solid 1px var(--primary);
}

.form input {
    border-radius: 5px 0 0 5px;
    padding: 1.25rem 2.5rem;
}

.form button {
    border-radius: 0 5px 5px 0;
    background: var(--primary);
    color: #FFF;
    padding: 1.25rem;
}

.form button:hover {
    background: var(--midtone);
}

.todos {
    width: 60%;
    background: var(--primary);
  }
